  border-radius: 4px;
  border: 1px solid #ddd;
  padding: 0 20px;
  font-size: 14px;
}

form#new-post button {
  padding: 10px 20px;
  border-radius: 4px;
  border: 0;
  /* background: #9930d6; */
  background: #9972c7;
  color: #FFF;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  width: 50%;
  margin: 0 auto;
  background-image:-webkit-gradient(linear, left top, left bottom, from(transparent), to(rgba(0,0,0,.1)));
  background-image:linear-gradient(to bottom, transparent, rgba(0,0,0,.1));

}

form#new-post select {
  height: 38px;
  border-radius: 4px;
  border: 1px solid #ddd;
  padding: 0 20px;
  font-size: 14px;
  margin-bottom: 20px;
}

form#new-post p {
  font-size: 18px;
  font-weight: bold;
  border-bottom: 1px solid #9972c7;
  /* border-radius: 10px; */
  line-height: 40px;
  margin-bottom: 20px;
  text-align: center;
  color: #9972c7;
}

.children {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}


form#new-post input[type=text]#comment {
  height: 110px;
  border-radius: 5px;
  border: 1px solid #ddd;
  padding: 0 20px;
  font-size: 14px;
  margin-top: 20px;
  margin-bottom: 20px;
}

form#new-post input[type=text]#description {
  height: 110px;
  border-radius: 5px;
  border: 1px solid #ddd;
  padding: 0 20px;
  font-size: 14px;
}

form#new-post.children#gridchild {
  font-size: 18px;
  font-weight: bold;
  line-height: 40px;
  margin-bottom: 20px;
  text-align: center;
  color: #9972c7;
}

form#new-post button#btn-logout {
  background: #DB587C;
}

