/*BOTAO 1*/

.switch {
  visibility: hidden;
  position: absolute;
  margin-left: -9999px;
}

.switch + label {
  display: block;
  position: relative;
  cursor: pointer;
  outline: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}


/* Estilo Flat */
.switch--flat + label {
  padding: 2px;
  width: 31px;
  height: 20px;
  background-color: #dddddd;
  border-radius: 60px;
  -webkit-transition: background 0.4s;
  transition: background 0.4s;
}
.switch--flat + label:before,
.switch--flat + label:after {
  display: block;
  position: absolute;
  content: "";
}
.switch--flat + label:before {
  top: 2px;
  left: 2px;
  bottom: 2px;
  right: 2px;
  background-color: #fff;
  border-radius: 60px;
  -webkit-transition: background 0.4s;
  transition: background 0.4s;
}
.switch--flat + label:after {
  top: 4px;
  left: 4px;
  bottom: 4px;
  width: 20px;
  height:12px;
  background-color: #dddddd;
  border-radius: 52px;
  -webkit-transition: margin 0.4s, background 0.4s;
  transition: margin 0.4s, background 0.4s;
}
.switch--flat:checked + label {
  background-color: #e36cee;
}
.switch--flat:checked + label:after {
  margin-left: 5px;
  background-color: #e36cee;
}


.switch switch--flat {
  margin-left: 52px;
}


