section#post-list {
  width: 100%;
  max-width: 580px;
  margin: 0 auto;
  padding: 0 30px;
  margin-top: 80px;
}

section#post-list article {
  background: #fff;
  border: 1px solid #ddd;
  margin-top: 30px;
  box-shadow: 0px 20px 50px 0px rgba(0, 0, 0, 0.12);
  border-radius: 10px;
}

section#post-list article.feedunique {
  background: #fff;
  border: 1px solid #ddd;
  margin-top: 30px;
  box-shadow: 0px 20px 50px 0px rgba(0, 0, 0, 0.12);
  border-radius: 20px;
  background-image: linear-gradient(
    to bottom,
    transparent,
    rgba(0, 0, 0, 0.05)
  );
}

section#post-list article header {
  padding: 0px;

  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: 20px;
  margin-right: -15px;
}

section#post-list article header .user-info {
  display: flex;
  flex-direction: column;
}

section#post-list article header .user-info span {
  font-size: 15px;
}

section#post-list article header .user-info.parent {
  font-size: 15px;
  margin-top: 15px;
  margin-bottom: 15px;
}

section#post-list article header .user-info.post-unique {
  font-size: 15px;
  margin-top: 20px;
  margin-bottom: 0px;
}

section#post-list article > img {
  width: 100%;
}

section#post-list article footer {
  padding: 20px;
}

section#post-list article footer .actions {
  margin-top: -10px;
  margin-bottom: -15px;
}

footer strong {
  font-size: 12px;
}

section#post-list article footer .actions button {
  background: transparent;
  border: 0;
  cursor: pointer;
}

section#post-list article footer .actions img {
  height: 20px;
  margin-right: 10px;
  margin-bottom: 10px;
}

section#post-list article footer p {
  font-size: 15px;
  margin-top: 10px;
  line-height: 10px;
}

section#post-list article footer p#timestamp {
  font-size: 11px;
}

section#post-list article footer p span {
  color: #9972c7;
  display: block;
}
